<script>
import TinymceEditor from "./tinymce-editor";

export default {
  components: {
    TinymceEditor
  },
  data() {
    return {
      priceModel: "average",
      isLogin: false,
      lowestMarketPrice: 26012.921,
      tableStr1_1: " ",
      tableStr1_2: " ",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        //zhende
        isBuy: true,
        offerType: "",
        currency: "",
        radio: true,
        offerPrice: 26012.921,
        isFixed: true,
        offerPrice_2: 26012.921,
        offerPriceRate: 0,
        minPrice: 100,
        maxPrice: 1000,
        checkList: [],
        tinymceHtml:
          "<p>1. 15</p><p> 2. 15</p><p> 3. </p>"
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    fomatFloat(value, n) {
      var f = Math.round(value * Math.pow(10, n)) / Math.pow(10, n);
      var s = f.toString();
      var rs = s.indexOf(".");
      if (rs < 0) {
        s += ".";
      }
      for (var i = s.length - s.indexOf("."); i <= n; i++) {
        s += "0";
      }
      return s;
    },
    formatTooltip(val) {
      return val / 100;
    }
  },
  mounted() {
    tinymce.init({});
    var user = sessionStorage.getItem("user");
    if (user) {
      this.isLogin = true;
    }
  },
  computed: {
    tableStr1: function() {
      return this.form.isBuy ? this.tableStr1_1 : this.tableStr1_2;
    },
    defaultPrice: function() {
      return parseFloat(
        this.fomatFloat(parseFloat(this.lowestMarketPrice) * 0.94, 3)
      );
    },
    floatPrice: function() {
      return parseFloat(
        this.fomatFloat(
          (this.lowestMarketPrice * (100 + this.form.offerPriceRate)) / 100,
          3
        )
      );
    }
  }
};
</script>
<style scoped>
.tableRow {
  margin-top: 11px;
}
.firstRow {
  margin: 2%;
}
.bigText {
  margin: 0 0 20px;
  border-left: 5px solid #43cb83;
  padding-left: 10px;
  font-size: 28px;
}
.normalText {
  margin: 0 0 10px;
}
.greenText {
  color: #2a985e;
  margin: 5px 0;
}
.greenTextBig {
  color: #2a985e;
  margin: 5px 0;
  font-size: 24px;
}
.greenTextSmall {
  color: #2a985e;
  margin: 5px 0;
  font-size: 10px;
}
.bigText2 {
  margin: 10px 0;
  font-size: 20px;
}
</style>



